<template lang="">
  <h2>Hello world {{ x }} {{ startTime }}</h2>
  <button @click=startTimer>Start</button>
  <br>
  <input type="text" ref="minutes" placeholder="minutes">
  <input type="text" ref="seconds" placeholder="seconds">
  <input type="button" value="Run" @click=setTimer>
  <h3 :class="{timerRanOut:!timerRunning}">{{ displayTimeRemaining }}</h3>
</template>
<script>
const v = require('voca');

export default {
  data() {return {
    startTime: null,
    timerEndTime: null,
    x: 11,
    displayTimeRemaining: "0:00",
    UIInterval: null,
    timerRunning: false,
  }},
  methods: {
    startTimer() {
      this.x += 1
      this.startTime = Date.now()
    },
    setTimer() {
      const seconds = this.$refs.seconds.value
      const minutes = this.$refs.minutes.value
      this.timerEndTime = Date.now() + 1000*seconds + 60*1000*minutes
    },
    updateUI() {
      const remainingTime = this.timerEndTime - Date.now()
      if (this.timerEndTime == null) {
        this.displayTimeRemaining = this.formatTime(0)
        return
      }
      this.timerRunning = !(remainingTime < 0 )
      this.displayTimeRemaining = this.formatTime(remainingTime)
    },
    formatTime(ms) {
      const isNegative = ms < 0
      if (isNegative) {
        ms = -ms
      }
      const seconds = ms/1000
      var numhours = Math.floor(((seconds % 31536000) % 86400) / 3600);
      var numminutes = Math.floor((((seconds % 31536000) % 86400) % 3600) / 60);
      var numseconds = (((seconds % 31536000) % 86400) % 3600) % 60;
      return v.sprintf('%01d:%02d:%05.2f', numhours, numminutes, numseconds)
    },
    playAudio() {
      var audio = new Audio('https://assets.mixkit.co/sfx/preview/mixkit-doorbell-tone-2864.mp3');
      audio.play();
    }
  },
  watch: {
    timerRunning(newVal) {
      if (newVal == true) {
        return
      }
      this.playAudio()
    }
  },
  mounted() {
    this.UIInterval = setInterval(this.updateUI, 79)
  },
  unmounted() {
    clearInterval(this.UIInterval)
  },
}
</script>
<style lang="css">
.timerRanOut {
  color: red;
}
</style>